/* Reset and base styles  */


*,
*::before,
*::after {
	box-sizing: border-box;
    font-family: 'Times New Roman', Times, serif;
    font-weight: normal;
}

body {
	padding: 0px;
	margin: 0px;
	background: linear-gradient(to right, #00AAFF, #00FF6C);
}



